<template>
  <div class="container">
    <h1 class="title">实时数据监控</h1>
    <mid-card class="card1">
      <template #img>
        <img src="@/img/交换机 (1).png" style="width: 100%; height: 100%" />
      </template>
      <template #title>已上线交换机</template>
      <template #number>2</template>
    </mid-card>
    <mid-card class="card2">
      <template #img>
        <img src="@/img/ATM路由器 (1).png" style="width: 100%; height: 100%" />
      </template>
      <template #title>已上线路由器</template>
      <template #number>2</template>
    </mid-card>
    <mid-card class="card3">
      <template #img>
        <img src="@/img/应用系统.png" style="width: 100%; height: 100%" />
      </template>
      <template #title>已上线主机</template>
      <template #number>2</template>
    </mid-card>
    <h1 class="info" v-if="attack">攻击</h1>
    <h1 class="info" style="color: #71ce71" v-else>保护</h1>
    <div
      class="cicrle"
      v-if="attack"
      :style="{ '--infoColor': attackColor }"
    ></div>
    <div class="cicrle" v-else :style="{ '--infoColor': normolColor }"></div>
  </div>
</template>

<script>
import MidCard from "@/view/common/MidCard.vue";
import "@/utils/eventbus.js";
export default {
  data() {
    return {
      attack: false,
      attackColor: "rgba(183, 44, 18, 0.5)",
      normolColor: "rgba(112, 204, 112, 0.5)",
    };
  },
  components: {
    MidCard,
  },
  created() {
    this.socktServe();
  },
  methods: {
    socktServe() {
      if (typeof WebSocket == "undefined") {
        console.log("您的浏览器不支持WebSocket");
      } else {
        console.log("您的浏览器支持WebSocket");
        //实现化WebSocket对象，指定要连接的服务器地址与端口  建立连接
        //等同于socket = new WebSocket("ws://localhost:8888/xxxx/im/25");
        //var socketUrl="${request.contextPath}/im/"+$("#userId").val();
        var socketUrl = "ws://192.168.46.128:5678";
        socketUrl = socketUrl.replace("https", "ws").replace("http", "ws");
        console.log(socketUrl);
        console.log("连接虚拟机");

        var socket = new WebSocket(socketUrl);
        //打开事件
        socket.onopen = function () {
          console.log("websocket已打开");
          //socket.send("这是来自客户端的消息" + location.href + new Date());
        };
        //获得消息事件
        socket.onmessage = (msg) => {
          // console.log(msg.data);
          var res = JSON.parse(msg.data);
          console.log(res);
          if (res.flag == 1) {
            this.attack = true;
            this.$eventbus.$emit("flag1", res.date);
          } else if (res.flag == 0) {
            this.attack = false;
            this.$eventbus.$emit("flag0", res.date);
          }
          //发现消息进入    开始处理前端触发逻辑
        };
        //关闭事件
        socket.onclose = function () {
          console.log("websocket已关闭");
        };
        //发生了错误事件
        socket.onerror = function () {
          console.log("websocket发生了错误");
        };
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  background-image: url(~@/assets/mid4x.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.title {
  position: absolute;
  left: 50%;
  top: 95px;
  transform: translate(-50%);
  color: #70c2fb;
  font-size: 25px;
}
.card1 {
  position: absolute;
  top: 180px;
  left: 95px;
  width: 200px;
  height: 100px;
}

.card2 {
  position: absolute;
  top: 300px;
  left: 95px;
  width: 200px;
  height: 100px;
}
.card3 {
  position: absolute;
  top: 240px;
  left: 615px;
  width: 200px;
  height: 100px;
}
.info {
  position: absolute;
  top: 280px;
  left: 425px;
  font-size: 25px;
  color: red;
  animation: blink 1s infinite steps(1);
}
@keyframes blink {
  // 50% {
  //   color: transparent;
  // }
  0% {
    // transform: scale(0.5);
    opacity: 0;
  }

  30% {
    // transform: scale(0.7);
    opacity: 1;
  }

  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}
/* 设置动画后颜色 */
.cicrle:before {
  // background-color: rgba(183, 44, 18, 0.5);
  background-color: var(--infoColor);
}

.cicrle:before,
.cicrle:after {
  content: "";
  width: 250px;
  height: 250px;
  position: absolute;
  left: 330px;
  top: 170px;
  border-radius: 50%;
  animation: warn 1s ease-out 0s infinite;
}

@keyframes warn {
  0% {
    transform: scale(0.5);
    opacity: 1;
  }

  30% {
    transform: scale(0.7);
    opacity: 1;
  }

  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}
</style>
