<template>
  <div>
    <my-card class="card">
      <template #title>日志信息</template>
      <template>
        <div class="log">
          <el-table
            :data="tableData"
            max-height="190"
            :row-class-name="tableRowClassName"
            :header-cell-style="{
              background: '#0A1B2B',
              color: '#fff',
              border: 'none',
            }"
            :cell-style="{
              color: '#fff',
              border: 'none',
            }"
          >
            <el-table-column prop="date" label="时间" width="160">
            </el-table-column>
            <el-table-column prop="attackip" label="来源ip" width="90">
            </el-table-column>
            <el-table-column prop="attackuser" label="来源用户" width="80">
            </el-table-column>
            <el-table-column prop="targetdevice" label="目标设备" width="80">
            </el-table-column>
          </el-table>
        </div>
        <div
          id="chart"
          style="height: 300px; width: 450px; margin-top: 180px"
        ></div>
      </template>
    </my-card>
  </div>
</template>

<script>
import MyCard from "../common/ScreenCard.vue";
import * as echarts from "echarts";
export default {
  components: {
    MyCard,
  },
  data() {
    return {
      tableData: [],
    };
  },
  methods: {
    loadDeviceLog() {
      this.$axios
        .get(this.$httpUrl + "/devicelog")
        .then((res) => res.data)
        .then((res) => {
          console.log(res);
          this.tableData = res;
        });
    },
    tableRowClassName({ rowIndex }) {
      if (rowIndex % 2 === 0) {
        return "warning-row";
      } else {
        return "success-row";
      }
    },
    initChart() {
      var chartDom = document.getElementById("chart");
      var myChart = echarts.init(chartDom);

      const targetCoord = [1000, 140];
      const curveness = 0.2;
      const linesData = [];
      const categories = [
        {
          name: "流入中",
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  offset: 0,
                  color: "#01acca",
                },
                {
                  offset: 1,
                  color: "#5adbe7",
                },
              ]),
            },
          },
          label: {
            normal: {
              fontSize: "14",
            },
          },
        },
        {
          name: "暂无流入",
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  offset: 0,
                  color: "#ffb402",
                },
                {
                  offset: 1,
                  color: "#ffdc84",
                },
              ]),
            },
          },
          label: {
            normal: {
              fontSize: "14",
            },
          },
        },
      ];

      const item = {
        name: "网络中心",
        value: targetCoord,
        symbolSize: 40,
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
              {
                offset: 0,
                color: "#157eff",
              },
              {
                offset: 1,
                color: "#35c2ff",
              },
            ]),
          },
        },
        label: {
          normal: {
            fontSize: "14",
          },
        },
      };

      const items = [
        {
          name: "H1",
          category: 0,
          active: true,
          speed: "50kb/s",
          value: [0, 0],
        },
        {
          name: "H2",
          category: 0,
          active: true,
          speed: "50kb/s",
          value: [0, 100],
        },
        {
          name: "H3",
          category: 1,

          value: [0, 200],
        },
        {
          name: "H4",
          category: 1,
          value: [0, 300],
        },
      ];

      const data = items.concat([item]);

      items.forEach(function (el) {
        if (el.active) {
          linesData.push([
            {
              coord: el.value,
            },
            {
              coord: targetCoord,
            },
          ]);
        }
      });

      const links = items.map((el) => {
        return {
          source: el.name,
          target: item.name,
          speed: el.speed,
          lineStyle: {
            normal: {
              color: el.speed ? "#12b5d0" : "#ff0000",
              curveness: curveness,
            },
          },
        };
      });

      var option = {
        // legend: [
        //   {
        //     formatter: function (name) {
        //       return echarts.format.truncateText(
        //         name,
        //         100,
        //         "14px Microsoft Yahei",
        //         "…"
        //       );
        //     },
        //     tooltip: {
        //       show: true,
        //     },
        //     textStyle: {
        //       color: "#999",
        //     },
        //     selectedMode: false,
        //     right: 0,
        //     data: categories.map(function (el) {
        //       return el.name;
        //     }),
        //   },
        // ],
        xAxis: {
          show: false,
          type: "value",
        },
        yAxis: {
          show: false,
          type: "value",
        },
        series: [
          {
            type: "graph",
            layout: "none",
            coordinateSystem: "cartesian2d",
            symbolSize: 25,
            z: 3,
            edgeLabel: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: 14,
                },
                formatter: function (params) {
                  let txt = "";
                  if (params.data.speed !== undefined) {
                    txt = params.data.speed;
                  }
                  return txt;
                },
              },
            },
            label: {
              normal: {
                show: true,
                position: "bottom",
                color: "#5e5e5e",
              },
            },
            itemStyle: {
              normal: {
                shadowColor: "none",
              },
              emphasis: {},
            },
            lineStyle: {
              normal: {
                width: 2,
                shadowColor: "none",
              },
            },
            edgeSymbol: ["none", "arrow"],
            edgeSymbolSize: 8,
            data: data,
            links: links,
            categories: categories,
          },
          {
            name: "A",
            type: "lines",
            coordinateSystem: "cartesian2d",
            z: 1,
            effect: {
              show: true,
              smooth: false,
              trailLength: 0,
              symbol: "arrow",
              color: "rgba(55,155,255,0.5)",
              symbolSize: 12,
            },
            lineStyle: {
              normal: {
                curveness: curveness,
              },
            },
            data: linesData,
          },
        ],
      };
      myChart.setOption(option);
    },
  },
  mounted() {
    // this.loadDeviceLog();
    this.loadDeviceLog();
    this.initChart();
  },
};
</script>

<style lang="scss" scoped>
.card {
  width: 450px;
  height: 500px;
}
.log {
  position: absolute;
  top: 20px;
  height: 60%;
  width: 410px;
  padding: 0 20px;
}

::v-deep .el-table .warning-row {
  background: #122a3b;
}

::v-deep .el-table .success-row {
  background: #102535;
}
::v-deep .el-table {
  background-color: transparent;
}
// 修改表格最底边框
// .el-table::before {
//   height: 0;
// }

// 改变滚动条样式
// 设置滚动条的宽度
::v-deep .el-table__body-wrapper::-webkit-scrollbar {
  width: 3px;
  background-color: #0a1b2b;
}
// 设置滚动条的背景色和圆角
::v-deep .el-table__body-wrapper::-webkit-scrollbar-thumb {
  background-color: #89fbf4;
  border-radius: 8px;
}
// 修改hover
::v-deep {
  .el-table {
    tbody tr {
      &:hover {
        td {
          background-color: #316174 !important;
        }
      }
    }
  }
}
::v-deep.el-table {
  th,
  tr,
  td {
    border: none;
    background-color: transparent;
  }
  // 底部边框
  &::before {
    height: 0px;
  }
}
</style>
