<template>
  <div class="container">
    <h1 class="title">系统运行时间</h1>
    <div v-for="(item, index) in str" :key="item.id" class="time">
      <time-item>
        <template v-if="index == 0 && timeArr">
          <span>{{ timeArr[0] }}</span>
        </template>
        <template v-if="index == 1"><span>天</span></template>
        <template v-if="index == 2 && timeArr">
          <span>{{ timeArr[1] }}</span>
        </template>
        <template v-if="index == 3"><span>时</span></template>
        <template v-if="index == 4 && timeArr">
          <span>{{ timeArr[2] }}</span>
        </template>
        <template v-if="index == 5"><span>分</span></template>
        <template v-if="index == 6 && timeArr">
          <span>{{ timeArr[3] }}</span>
        </template>
        <template v-if="index == 7"><span>秒</span></template>
      </time-item>
    </div>
  </div>
</template>

<script>
import TimeItem from "./TimeItem.vue";
// import * as moment from "moment";
export default {
  data() {
    return {
      str: "12345678",
      timeArr: null,
    };
  },
  components: {
    TimeItem,
  },
  created() {
    // var res = moment([2023, 5, 23]).fromNow(true);
    // console.log(res);
    setInterval(() => {
      var time = this.time();
      this.timeArr = time;
      // console.log(this.timeArr);
    });
  },
  methods: {
    time: function () {
      // window.setTimeout("time()", 1000);
      var before = new Date("5/20/2023 00:00:00");
      //获取当前时间
      var today = new Date();
      var timeold = today.getTime() - before.getTime(); //总豪秒数
      // var secondsold = Math.floor(timeold / 1000); //总秒数
      var e_daysold = timeold / (24 * 60 * 60 * 1000);
      var daysold = Math.floor(e_daysold); //相差天数
      var e_hrsold = (e_daysold - daysold) * 24;
      var hrsold = Math.floor(e_hrsold); //相差小时数
      var e_minsold = (e_hrsold - hrsold) * 60;
      var minsold = Math.floor(e_minsold); //相差分钟数
      var seconds = Math.floor((e_minsold - minsold) * 60); //相差秒数
      return [daysold, hrsold, minsold, seconds];
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
}
.time {
  float: left;
}
.title {
  position: absolute;
  top: -60px;
  left: 50%;
  transform: translate(-50%);
  color: #00fff4;
}
</style>
