<template>
  <div>
    <my-card class="card1">
      <template #title>全局数据包</template>
      <template style="position: relative">
        <div id="chart1" style="height: 380px; width: 450px"></div>
      </template>
    </my-card>
    <my-card class="card2">
      <template #title>全局数据流量</template>
      <template>
        <div id="chart2" style="height: 380px; width: 840px"></div>
      </template>
    </my-card>
    <my-card class="card3">
      <template #title>端口流量</template>
      <template>
        <div id="chart3" style="height: 380px; width: 450px"></div>
      </template>
    </my-card>
  </div>
</template>

<script>
import * as echarts from "echarts";
import MyCard from "../common/ScreenCard.vue";
export default {
  name: "MyBottom",
  data() {
    return {
      chart2: null,
      chart2Data1: [],
      chart2Data2: [],
      chart1: null,
      chart1Data1: [],
      chart1Data2: [],
      chart3: null,
      chart3Data1: [],
      chart3Data2: [],
      chart3Data3: [],
      chart3Data4: [],
    };
  },
  components: {
    MyCard,
  },
  created() {
    // this.socktServe();
    console.log("btmcreated");
  },
  mounted() {
    this.initChart4();
    this.initChart2();
    this.initChart3();
    this.socktServe();
  },
  activated() {
    console.log("btmactive");
  },
  beforeDestroy() {
    console.log("btmdes");
  },
  methods: {
    initChart1: function () {
      var chartDom = document.getElementById("chart1");
      var myChart = echarts.init(chartDom);
      var option;

      function randomData() {
        now = new Date(+now + oneDay);
        value = value + Math.random() * 21 - 10;
        return {
          name: now.toString(),
          value: [
            [now.getFullYear(), now.getMonth() + 1, now.getDate()].join("/"),
            Math.round(value),
          ],
        };
      }
      let data = [];
      let now = new Date(1997, 9, 3);
      let oneDay = 24 * 3600 * 1000;
      let value = Math.random() * 1000;
      for (var i = 0; i < 1000; i++) {
        data.push(randomData());
      }
      option = {
        title: {
          show: false,
          text: "端口流量图",
        },
        tooltip: {
          trigger: "axis",
          formatter: function (params) {
            params = params[0];
            var date = new Date(params.name);
            return (
              date.getDate() +
              "/" +
              (date.getMonth() + 1) +
              "/" +
              date.getFullYear() +
              " : " +
              params.value[1]
            );
          },
          axisPointer: {
            animation: false,
          },
        },
        xAxis: {
          type: "time",
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          name: "packet/s",
          nameLocation: "center",
          nameGap: 35,
          nameTextStyle: {
            fontSize: 20,
          },
          type: "value",
          boundaryGap: [0, "100%"],
          splitLine: {
            show: false,
          },
        },
        series: [
          {
            name: "Fake Data",
            type: "line",
            showSymbol: false,
            data: data,
          },
        ],
      };
      setInterval(function () {
        for (var i = 0; i < 5; i++) {
          data.shift();
          data.push(randomData());
        }
        myChart.setOption({
          series: [
            {
              data: data,
            },
          ],
        });
      }, 1000);

      option && myChart.setOption(option);
    },
    initChart2: function () {
      var chartDom = document.getElementById("chart2");
      this.chart2 = echarts.init(chartDom);
      var option;

      option = {
        title: {
          show: false,
          text: "Dynamic Data & Time Axis",
        },
        grid: {
          left: 80,
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: "none",
            },
            restore: {},
            saveAsImage: {},
          },
        },
        legend: {
          type: "scroll",
          top: 30,
          right: 80,
          itemWidth: 50,
          itemHeight: 20,
          textStyle: {
            color: "#ffffff",
            fontSize: 20,
          },
          data: [
            {
              name: "udp",
            },
            {
              name: "tcp",
            },
          ],
        },
        tooltip: {
          trigger: "axis",
          formatter: function (params) {
            params = params[0];
            var date = new Date(params.name);
            return (
              date.getDate() +
              "/" +
              (date.getMonth() + 1) +
              "/" +
              date.getFullYear() +
              " : " +
              params.value[1]
            );
          },
          axisPointer: {
            animation: false,
          },
        },
        xAxis: {
          type: "time",
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          name: "kb/s",
          nameLocation: "center",
          nameGap: 35,
          nameTextStyle: {
            fontSize: 20,
          },
          // nameRotate: 45,
          type: "value",
          // boundaryGap: [0, "100%"],
          splitLine: {
            show: false,
          },
          axisLabel: {},
        },
        dataZoom: [
          {
            type: "inside",
            // type: "slider",
            show: true,
            start: 0,
            end: 30,
            xAxisIndex: [0, 1],
          },
        ],
        series: [
          {
            name: "tcp",
            type: "line",
            showSymbol: false,
            data: this.chart2Data1,
          },
          {
            name: "udp",
            type: "line",
            showSymbol: false,
            data: this.chart2Data2,
          },
        ],
      };

      this.chart2.setOption(option);
    },
    initChart3: function () {
      var chartDom = document.getElementById("chart3");
      this.chart3 = echarts.init(chartDom);
      var option;

      option = {
        title: {
          show: false,
          text: "Dynamic Data & Time Axis",
        },
        grid: {
          left: 70,
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: "none",
            },
            restore: {},
            saveAsImage: {},
          },
        },
        legend: {
          type: "scroll",
          top: 20,
          right: 70,
          itemWidth: 20,
          itemHeight: 20,
          textStyle: {
            color: "#ffffff",
            fontSize: 20,
          },
          data: [
            {
              name: "port1",
            },
            {
              name: "port2",
            },
            {
              name: "port3",
            },
            {
              name: "port4",
            },
          ],
        },
        tooltip: {
          trigger: "axis",
          formatter: function (params) {
            params = params[0];
            var date = new Date(params.name);
            return (
              date.getDate() +
              "/" +
              (date.getMonth() + 1) +
              "/" +
              date.getFullYear() +
              " : " +
              params.value[1]
            );
          },
          axisPointer: {
            animation: false,
          },
        },
        xAxis: {
          type: "time",
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          name: "kb/s",
          nameLocation: "center",
          nameGap: 35,
          nameTextStyle: {
            fontSize: 20,
          },
          type: "value",
          // boundaryGap: [0, "100%"],
          splitLine: {
            show: false,
          },
        },
        dataZoom: [
          {
            type: "inside",
            // type: "slider",
            show: true,
            start: 0,
            end: 30,
            xAxisIndex: [0, 1],
          },
        ],
        series: [
          {
            name: "port1",
            type: "line",
            showSymbol: false,
            data: this.chart3Data1,
          },
          {
            name: "port2",
            type: "line",
            showSymbol: false,
            data: this.chart3Data2,
          },
          {
            name: "port3",
            type: "line",
            showSymbol: false,
            data: this.chart3Data3,
          },
          {
            name: "port4",
            type: "line",
            showSymbol: false,
            data: this.chart3Data4,
          },
        ],
      };

      this.chart3.setOption(option);
    },
    initChart4() {
      var chartDom = document.getElementById("chart1");
      this.chart1 = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: "axis",
          position: function (pt) {
            return [pt[0], "10%"];
          },
        },
        grid: {
          left: 70,
        },
        legend: {
          type: "scroll",
          top: 30,
          right: 80,
          itemWidth: 50,
          itemHeight: 20,
          textStyle: {
            color: "#ffffff",
            fontSize: 20,
          },
          data: [
            {
              name: "udpPacket",
            },
            {
              name: "tcpPacket",
            },
          ],
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: "none",
            },
            restore: {},
            saveAsImage: {},
          },
        },
        // xAxis: {
        //   type: "time",
        //   boundaryGap: false,
        //   // data: date,
        // },
        // yAxis: {
        //   type: "value",
        //   boundaryGap: [0, "100%"],
        // },
        xAxis: {
          type: "time",
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          name: "packet/s",
          nameLocation: "center",
          nameGap: 35,
          nameTextStyle: {
            fontSize: 20,
          },
          type: "value",
          // boundaryGap: [0, "100%"],
          splitLine: {
            show: false,
          },
        },
        // dataZoom: [
        //   {
        //     type: "inside",
        //     start: 0,
        //     end: 10,
        //   },
        //   // {
        //   //   start: 0,
        //   //   end: 10,
        //   // },
        // ],
        dataZoom: [
          {
            type: "inside",
            // type: "slider",
            show: true,
            start: 0,
            end: 30,
            xAxisIndex: [0, 1],
          },
        ],
        series: [
          {
            name: "tcpPacket",
            type: "line",
            symbol: "none",
            sampling: "lttb",
            itemStyle: {
              color: "rgb(247,58,62)",
            },
            areaStyle: {
              // color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              //   {
              //     offset: 0,
              //     color: "rgb(255, 158, 68)",
              //   },
              //   {
              //     offset: 1,
              //     color: "rgb(255, 70, 131)",
              //   },
              // ]),
              color: "rgba(247,58,62,0.5)",
            },
            data: this.chart1Data1,
          },
          {
            name: "udpPacket",
            type: "line",
            symbol: "none",
            sampling: "lttb",
            itemStyle: {
              color: "rgb(0,215,111)",
            },
            areaStyle: {
              // color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              //   {
              //     offset: 0,
              //     color: "rgb(53,111,232)",
              //   },
              //   {
              //     offset: 1,
              //     color: "rgb(20,196,250)",
              //   },
              // ]),
              color: "rgba(0,215,111,0.5)",
            },
            data: this.chart1Data2,
          },
        ],
      };

      option && this.chart1.setOption(option);
    },
    socktServe() {
      if (typeof WebSocket == "undefined") {
        console.log("您的浏览器不支持WebSocket");
      } else {
        console.log("您的浏览器支持WebSocket");
        //实现化WebSocket对象，指定要连接的服务器地址与端口  建立连接
        //等同于socket = new WebSocket("ws://localhost:8888/xxxx/im/25");
        //var socketUrl="${request.contextPath}/im/"+$("#userId").val();
        var socketUrl = "http://192.168.0.114:8090/ws/1";
        socketUrl = socketUrl.replace("https", "ws").replace("http", "ws");
        console.log(socketUrl);

        var socket = new WebSocket(socketUrl);
        //打开事件
        socket.onopen = function () {
          console.log("websocket已打开");
          //socket.send("这是来自客户端的消息" + location.href + new Date());
        };
        //获得消息事件
        // socket.onmessage = function (msg) {
        //   // console.log(msg.data, this.chart1Data1);
        // };
        // 得使用钩子函数， 不然无法访问到this数据
        socket.onmessage = (msg) => {
          var res = JSON.parse(msg.data);
          console.log(res);
          // chart2
          var item1 = {
            name: res.date,
            value: [res.date, res.tcp],
          };
          var item2 = {
            name: res.date,
            value: [res.date, res.udp],
          };
          // chart1
          var item3 = {
            name: res.date,
            value: [res.date, res.tcpPacket],
          };
          var item4 = {
            name: res.date,
            value: [res.date, res.udpPacket],
          };
          // chart3
          var item5 = {
            name: res.date,
            value: [res.date, res.port1],
          };
          var item6 = {
            name: res.date,
            value: [res.date, res.port2],
          };
          var item7 = {
            name: res.date,
            value: [res.date, res.port3],
          };
          var item8 = {
            name: res.date,
            value: [res.date, res.port4],
          };
          this.chart2Data1.push(item1);
          this.chart2Data2.push(item2);
          this.chart1Data1.push(item3);
          this.chart1Data2.push(item4);
          this.chart3Data1.push(item5);
          this.chart3Data2.push(item6);
          this.chart3Data3.push(item7);
          this.chart3Data4.push(item8);
          // console.log(this.chart1Data1);
          this.chart2.setOption({
            series: [
              {
                data: this.chart2Data1,
              },
              {
                data: this.chart2Data2,
              },
            ],
          });
          this.chart1.setOption({
            series: [
              {
                data: this.chart1Data1,
              },
              {
                data: this.chart1Data2,
              },
            ],
          });
          this.chart3.setOption({
            series: [
              {
                data: this.chart3Data1,
              },
              {
                data: this.chart3Data2,
              },
              {
                data: this.chart3Data3,
              },
              {
                data: this.chart3Data4,
              },
            ],
          });
        };
        //关闭事件
        socket.onclose = function () {
          console.log("websocket已关闭");
        };
        //发生了错误事件
        socket.onerror = function () {
          console.log("websocket发生了错误");
        };
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.card1 {
  float: left;
  width: 450px;
  height: 420px;
}
.card2 {
  float: left;
  width: 840px;
  height: 420px;
  margin: 0 40px;
}
.card3 {
  float: left;
  width: 450px;
  height: 420px;
}
</style>
