<template>
  <div>
    <my-card class="card">
      <template #title>检测记录</template>
      <template>
        <el-steps
          :active="active"
          finish-status="success"
          class="timeline"
          direction="vertical"
        >
          <el-step
            title="正在监控网络状态"
            :description="time.time1"
            style="margin-bottom
          "
          >
            <!-- <template #title>
              <div>222222</div>
            </template> -->
          </el-step>
          <el-step title="检测到网络攻击" :description="time.time2"></el-step>
          <el-step title="正在处理网络攻击" :description="time.time3"></el-step>
          <el-step title="处理成功" :description="time.time4"></el-step>
        </el-steps>
        <el-button
          style="margin-top: 10px; margin-left: 300px; opacity: 0"
          @click="next"
        ></el-button>
        <div
          id="attack"
          style="height: 300px; width: 450px; margin-top: 140px"
        ></div>
      </template>
    </my-card>
  </div>
</template>

<script>
import MyCard from "../common/ScreenCard.vue";
import * as echarts from "echarts";
import "@/utils/eventbus.js";
// import moment from "moment";
export default {
  data() {
    return {
      active: 1,
      // nowTime: "",
      time: {
        time1: "",
        time2: "",
        time3: "",
        time4: "",
      },
    };
  },
  components: {
    MyCard,
  },
  created() {
    // moment.locale("zh_CN");
    // console.log(moment().format("YYYY-MM-DD HH:mm:ss"));
    // var that = this;
    // setInterval(() => {
    //   that.nowTime = moment(new Date()).format("YYYY-MM-DD HH:mm:ss");
    // }, 1000);
  },
  mounted() {
    var that = this;
    // that.setTime(1, this.nowTime);

    this.initChart();

    this.$eventbus.$on("flag1", (e) => {
      console.log("flag change1", that.active);
      that.active = 2;
      // that.time.time2 = e;
      that.setTime(2, e);
      setTimeout(() => {
        that.active = 3;
        // that.time.time3 = e;
        that.setTime(3, e);
      }, 1000);
    });
    this.$eventbus.$on("flag0", (e) => {
      console.log("flag change0", that.active);
      that.active = 4;
      // that.time.time4 = e;
      that.setTime(4, e);
      setTimeout(() => {
        that.active = 1;
        that.setTime(1, that.nowTime);
      }, 1000);
    });
    // this.$eventbus.$on("flag0", function () {
    //   console.log("flag change0", that.active);
    //   that.active = 4;
    //   setTimeout(() => {
    //     that.active = 1;
    //   }, 1000);
    // });
  },
  methods: {
    next() {
      if (this.active++ > 3) this.active = 1;
    },
    setTime(index, str) {
      var key = "time" + index;
      this.time[key] = str;
      for (let i = 1; i <= 4; i++) {
        if (i !== index) {
          let num = "time" + i;
          console.log(num);
          this.time[num] = "";
        }
      }
    },
    initChart() {
      var chartDom = document.getElementById("attack");
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: "item",
        },
        // legend: {
        //   // bottom: "28%",
        //   left: "center",
        //   // doesn't perfectly work with our tricks, disable it
        //   selectedMode: false,
        // },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: ["40%", "70%"],
            center: ["50%", "70%"],
            // adjust the start angle
            startAngle: 180,
            label: {
              show: true,
              formatter(param) {
                // correct the percentage
                return param.name + " (" + param.percent * 2 + "%)";
              },
            },
            data: [
              { value: 1048, name: "防护成功" },
              { value: 735, name: "失败" },
              {
                // make an record to fill the bottom 50%
                value: 1048 + 735,
                itemStyle: {
                  // stop the chart from rendering this piece
                  color: "none",
                  decal: {
                    symbol: "none",
                  },
                },
                label: {
                  show: false,
                },
              },
            ],
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  width: 450px;
  height: 500px;
}
.timeline {
  position: absolute;
  top: 30px;
  left: 20px;
  width: 450px;
  height: 200px;
}
</style>
